<template>
  <div class="page-card-content" v-loading="loading">
    <div class="top-area">
      <el-form ref="searchForm" :model="searchForm" inline size="middle" style="width:100%">
        <div style="display:flex">
          <div style="flex:1">
            <el-form-item label="账号">
              <el-input
                v-model="searchForm.keyword"
                placeholder="输入搜索关键字"
                clearable
                class="mr50"
              >
              </el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item>
              <el-button type="primary" @click="getTableData(true)">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="resetData()">重置</el-button>
            </el-form-item>
          </div>
        </div>
      </el-form>
    </div>

    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="650"
      @select="handlSelect"
      @select-all="handlSelect"
    >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="账号" show-overflow-tooltip prop="account"> </el-table-column>
      <el-table-column label="使用凭证" prop="token" width="450"> </el-table-column>
      <el-table-column label="登录时间" prop="createTime"> </el-table-column>
      <el-table-column label="在线时长" prop="loginDuration"
        ><template v-slot="scope">
          <span>{{ scope.row.loginDuration | filterRunningTime }}</span>
        </template>
      </el-table-column>
      <el-table-column label="平台" prop="platformName"> </el-table-column>
    </el-table>

    <el-pagination
      class="main-pagination"
      :total="pageInfo.total"
      :current-page="searchForm.pageNum"
      :page-size="searchForm.pageSize"
      :page-sizes="pageInfo.pageSizes"
      @current-change="handlePageChanged"
      @size-change="handlePageSizeChanged"
      background
      layout="total, sizes, prev, pager, next"
    />
  </div>
</template>

<script>
import API from '@/api/index.js'
export default {
  filters: {
    filterRunningTime(val) {
      if (val) {
        var hours = parseInt(val / (60 * 60))
        var minutes = parseInt((val % (60 * 60)) / 60)
        var seconds = parseInt(val % 60)
        if (minutes < 10) {
          minutes = '0' + minutes
        }
        if (seconds < 10) {
          seconds = '0' + seconds
        }
        return hours + ':' + minutes + ':' + seconds
      } else {
        return '--'
      }
    }
  },
  data() {
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      searchForm: {
        keyword: '',
        pageNum: 1,
        pageSize: 10
      }
    }
  },
  created() {
    this.getTableData()
  },
  mounted() {},
  methods: {
    resetData() {
      this.searchForm = {
        keyword: '',
        pageNum: 1,
        pageSize: 10
      }
      this.getTableData()
    },
    handlSelect(row) {
      this.clickRow = row
    },

    getTableData(init) {
      this.loading = true
      if (init) {
        this.searchForm.pageNum = 1
        this.searchForm.pageSize = 10
      }
      const form = JSON.parse(JSON.stringify(this.searchForm))

      if (!form.name) {
        delete form.name
      }
      API.getAccoutOnline(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data.records
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
          // if (this.$route.query.pageNum) {
          //   this.$router.replace('/dataManage/datasetManage/list')
          // }
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-table-column--selection .cell {
  padding-left: 10px;
}
#code {
  height: 500px;
  width: 500px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border: none;
}
.detailData {
  margin-bottom: 40px;
  div {
    line-height: 30px;
  }
}
::v-deep .el-upload-list {
  max-height: 400px;
  overflow: auto;
}
.mr50 {
  margin-right: 60px;
  width: 250px;
}
::v-deep .el-upload-dragger {
  width: 96px;
  height: 96px;
}
::v-deep .el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
::v-deep .el-upload--picture-card {
  width: 96px;
  height: 96px;
  line-height: 96px;
}

.labelWidth {
  width: 400px !important;
}
</style>
